.book {
  width: 220px;
  height: 300px;
  -webkit-transform: preserve-3d;
  -ms-transform: preserve-3d;
  transform: preserve-3d;
  perspective: 2000px;
  color: #000;
  background-color: #f5f5f5;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  box-shadow: 1px 1px 12px #000;
}

.cover {
  width: 100%;
  height: 100%;
  cursor: pointer;
  transform-origin: 0;
  background-color: #d3d3d3;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  transition: all .5s;
  display: flex;
  position: absolute;
  top: 0;
  box-shadow: 1px 1px 12px #000;
}

.book:hover .cover {
  transition: all .5s;
  transform: rotateY(-80deg);
}

p {
  font-size: 20px;
  font-weight: bolder;
}

.hover {
  width: 100%;
  justify-content: center;
  margin: 0 auto;
  display: flex;
}

